* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.banner {
  background-color: #CBDCFF;
  height: 44px;
}

.banner > p {
  margin-left: 10px;
}

.nav {
  margin-top: 20px;
}

.nav__search > label {
  padding: 16px;
  padding-top: 6px;
  padding-bottom: 9px;
  border-width: 1.6px;
  display: block;
  border-right-width: 0;
  border-color: #E0E3E3;
  border-style: solid;
  border-radius: 4px;
  border-radius: 4px 0 0 4px;
}

.nav__search > input {
  color: #5C5F60;
  font-size: 12px;
  height: 44px;
  width: 590px;
  border-width: 1.6px;
  border-left-width: 0;
  border-color: #E0E3E3;
  outline: none;
  padding-right: 10px;
  border-style: solid;
  border-radius: 0 4px 4px 0;
}

.nav__add {
  width: 130px;
  height: 44px;
  border-radius: 4px;
  background: #2D3132;
  border: none;
  color: #fff;
  font-size: 12px;
}

.nav__language > p {
  margin-right: 5px;
  font-size: 12px;
}

.nav__language {
  cursor: pointer;
}

.nav__actions button > .nav__actions-inactive {
  background: #EFF1F1 !important;
  color: #000;
  border: 2px solid #fff;
}

.nav__actions button > .nav__actions-active {
  background: #FF5449 !important;
  color: #fff;
  border: 2px solid #fff;
}

.nav__actions button {
  border: none;
  background: none;
}

.nav__actions button + button {
  margin-left: 25px;
}

.nav__user > img {
  margin-right: 8px;
}

.nav__user {
  cursor: pointer;
}

hr {
  margin-top: 20px;
  height: 1px;
  background: #E0E3E3;
}

.menu > .nav-item > .nav-link {
  color: #444748;
  font-weight: 500;
  font-size: 13px;
  letter-spacing: 0.4px;
  text-transform: uppercase;
}

.menu > .nav-item {
  padding: 16px;
  padding-top: 0px;
  position: relative;
}

.menu > .nav-item.active {
  border-bottom: 3px solid #444748;
}

.menu-breadcrumb {
  margin-top: 20px !important;
  font-size: 12px;
}

.menu-breadcrumb > ol > li > a {
  color: #747778;
  text-decoration: none;
}

.menu-breadcrumb > ol > .active {
  color: #2D3132 !important;
}

.catalog-title {
  font-size: 28px;
  color: #2D3132;
}

.catalog {
  margin-top: 20px;
}

.catalog__content {
  background: rgba(136, 136, 136, 0.118);
}

.catalog__filters {
  padding-right: 20px;
}

.catalog > .row {
  margin-left: 0;
  margin-right: 0;
}

.catalog__filters > h3 {
  color: #5C5F60;
  font-size: 22px;
  margin-bottom: 0;
  font-weight: normal;
}

.catalog__filters > .accordion {
  margin-top: 20px;
  border-radius: 0px;
}

.accordion-button {
  background-color: #EFF1F1 !important;
  border-radius: 0px !important;
  color: #444748;
  font-size: 14px;
}

.accordion-item {
  border-radius: 0px !important;
  border: 3px solid #EFF1F1;
}

.accordion-item + .accordion-item {
  margin-top: 2px;
}

.accordion-button:not(.collapsed) {
  color: #444748;
}

.accordion-button::after {
  background-image: var(--bs-accordion-btn-icon) !important;
}

.accordion-button:focus {
  box-shadow: 0 0 0 0.25rem #4447483d;
}